<h1 id="installation-d-un-serveur-web-sous-linux" tabindex="-1"><a class="header-anchor" href="#installation-d-un-serveur-web-sous-linux" aria-hidden="true">#</a> Installation d'un serveur web Sous linux</h1>
<h2 id="description" tabindex="-1"><a class="header-anchor" href="#description" aria-hidden="true">#</a> Description</h2>
<h2 id="apache" tabindex="-1"><a class="header-anchor" href="#apache" aria-hidden="true">#</a> Apache</h2>
<div class="language-bash ext-sh line-numbers-mode"><pre v-pre class="language-bash"><code><span class="token function">sudo</span> <span class="token function">apt-get</span> <span class="token function">install</span> apache2 build-essential
</code></pre><div class="line-numbers"><span class="line-number">1</span><br></div></div><h2 id="php" tabindex="-1"><a class="header-anchor" href="#php" aria-hidden="true">#</a> PHP</h2>
<div class="language-bash ext-sh line-numbers-mode"><pre v-pre class="language-bash"><code><span class="token function">sudo</span> <span class="token function">apt-get</span> <span class="token function">install</span> php libapache2-mod-php php-curl php-gd php-intl php-json php-mbstring php-xml php-zip sqlite3
</code></pre><div class="line-numbers"><span class="line-number">1</span><br></div></div><h2 id="mysql" tabindex="-1"><a class="header-anchor" href="#mysql" aria-hidden="true">#</a> MySQL</h2>
<div class="language-bash ext-sh line-numbers-mode"><pre v-pre class="language-bash"><code><span class="token function">sudo</span> <span class="token function">apt-get</span> <span class="token function">install</span> mysql-server php-mysql
</code></pre><div class="line-numbers"><span class="line-number">1</span><br></div></div><h2 id="maria-db" tabindex="-1"><a class="header-anchor" href="#maria-db" aria-hidden="true">#</a> Maria DB</h2>
<div class="language-bash ext-sh line-numbers-mode"><pre v-pre class="language-bash"><code><span class="token function">sudo</span> <span class="token function">apt-get</span> <span class="token function">install</span> mariadb-server php-mysql
</code></pre><div class="line-numbers"><span class="line-number">1</span><br></div></div><h2 id="phpmyadmin" tabindex="-1"><a class="header-anchor" href="#phpmyadmin" aria-hidden="true">#</a> Phpmyadmin</h2>
<div class="language-bash ext-sh line-numbers-mode"><pre v-pre class="language-bash"><code><span class="token function">sudo</span> <span class="token function">apt-get</span> <span class="token function">install</span> php-myadmin
</code></pre><div class="line-numbers"><span class="line-number">1</span><br></div></div><h2 id="configuration-de-lancement" tabindex="-1"><a class="header-anchor" href="#configuration-de-lancement" aria-hidden="true">#</a> Configuration de lancement</h2>
<p>Pour empêcher le demmarage automatique il faut :</p>
<div class="language-bash ext-sh line-numbers-mode"><pre v-pre class="language-bash"><code><span class="token function">sudo</span> systemctl disable apache2
<span class="token function">sudo</span> systemctl disable mysql
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br></div></div><p>Pour le lancement manuel</p>
<div class="language-bash ext-sh line-numbers-mode"><pre v-pre class="language-bash"><code><span class="token function">sudo</span> systemctl start apache2
<span class="token function">sudo</span> systemctl start mysql
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br></div></div><p>Pour réactiver le demmarage automatique.</p>
<div class="language-bash ext-sh line-numbers-mode"><pre v-pre class="language-bash"><code><span class="token function">sudo</span> systemctl <span class="token builtin class-name">enable</span> apache2
<span class="token function">sudo</span> systemctl <span class="token builtin class-name">enable</span> mysql
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br></div></div><h2 id="configuration-de-php" tabindex="-1"><a class="header-anchor" href="#configuration-de-php" aria-hidden="true">#</a> Configuration de PHP</h2>
<p>Pour que <strong>PHP</strong> fonctionne correctement il faut activer certaines extensions utiles. Il faut pour cela éditer le fichier <code>php.ini</code>.</p>
<div class="language-bash ext-sh line-numbers-mode"><pre v-pre class="language-bash"><code><span class="token function">sudo</span> <span class="token function">nano</span> /etc/php/7.2/cli/php.ini
</code></pre><div class="line-numbers"><span class="line-number">1</span><br></div></div><p>Il faut activer les extensions:</p>
<ul>
<li>curl</li>
<li>pdo_mysql</li>
<li>pdo_sqlite</li>
<li>sqlite3</li>
</ul>
<h2 id="autres-logiciels-utiles" tabindex="-1"><a class="header-anchor" href="#autres-logiciels-utiles" aria-hidden="true">#</a> Autres logiciels utiles</h2>
<h3 id="git" tabindex="-1"><a class="header-anchor" href="#git" aria-hidden="true">#</a> Git</h3>
<div class="language-language ext-language line-numbers-mode"><pre v-pre class="language-language"><code>sudo apt-get install git 
</code></pre><div class="line-numbers"><span class="line-number">1</span><br></div></div><h3 id="composer" tabindex="-1"><a class="header-anchor" href="#composer" aria-hidden="true">#</a> Composer</h3>
<div class="language-bash ext-sh line-numbers-mode"><pre v-pre class="language-bash"><code><span class="token function">sudo</span> <span class="token function">apt-get</span> <span class="token function">install</span> <span class="token function">composer</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br></div></div><h3 id="open-shh-server" tabindex="-1"><a class="header-anchor" href="#open-shh-server" aria-hidden="true">#</a> Open shh server</h3>
<div class="language-bash ext-sh line-numbers-mode"><pre v-pre class="language-bash"><code><span class="token function">sudo</span> <span class="token function">apt-get</span> <span class="token function">install</span> openssh-server
</code></pre><div class="line-numbers"><span class="line-number">1</span><br></div></div><h2 id="sources" tabindex="-1"><a class="header-anchor" href="#sources" aria-hidden="true">#</a> Sources</h2>
